@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.table > p {
  font-size: 16px;
  color: grey;
  width: 10%;
  text-align: center;
}

.table > img {
  border-radius: 50%;
  height: 35px;
  width: 35px;
}

.button_rehab1 {
  background-color: darkorchid;
  border-radius: 20px;
  border: none;
  outline: none;
  color: whitesmoke;
  font-family: Georgia, "Times New Roman", Times, serif;
}

.button_rehab2 {
  color: whitesmoke;
  font-family: Georgia, "Times New Roman", Times, serif;
  background-color: dodgerblue;
  border-radius: 20px;
  border: none;
  outline: none;
  margin: 0em 0.5em;
}

/* .MainDiv :nth-child(5) .button_rehab2 {
  background-color: red;
} */

.header_heading {
  color: rgb(66, 133, 244);
  font-size: 25px;
  font-weight: 700;
  margin: 1em;
}

.header_input {
  width: 400px;
  height: 40px;
  margin: 1em;
  outline: none;
  border: none;
  box-shadow: 0 4px 5px 0 rgba(0, 0, 0, 0.2), 0 6px 10px 0 rgba(0, 0, 0, 0.19);
  border-radius: 10px;
}

.header_patient {
  margin: 1em;
  text-align: center;
  border: none;
  box-shadow: 0 4px 5px 0 rgba(0, 0, 0, 0.2), 0 6px 10px 0 rgba(0, 0, 0, 0.19);
  border-radius: 5px;
  /* padding: 0em; */
}

.header_Button {
  margin: 1em auto;
  text-align: center;
  border-radius: 10px;
  background-color: rgb(66, 133, 244);
  color: #ffff;
  font-weight: 600;
  border: none;
}

.header_right {
  padding: 1em;
}

.ant-form-item-label label {
  font-size: 16px;
  color: rgb(66, 133, 244);
  font-family: Poppins;
}

